import {
  JupyterFrontEnd,
  JupyterFrontEndPlugin
} from '@jupyterlab/application';

import { CorePlugin } from '@anaconda/core_lib';

import { tokens, CoreExtension } from '@anaconda/shared';

const corePlugin = new CorePlugin();

const plugin: JupyterFrontEndPlugin<CoreExtension> = {
  id: '@anaconda/core:plugin',
  description: 'Common Anaconda extension functionality',
  autoStart: true,
  provides: tokens.CoreToken,
  activate: (app: JupyterFrontEnd): CoreExtension => {
    console.log('JupyterLab extension @anaconda/core is activated!');
    corePlugin.activate(app);
    return { corePlugin };
  }
};

export default plugin;
